import config from '../config.json';
import * as storage from './storage';
import * as signalR from '@aspnet/signalr';
import { saveAs } from 'file-saver';

const DEFAULT_FILTERS = [];
const DEFAULT_PAGE = { limit: 1000, skip: 0 };
const DEFAULT_SORT = { field: '_id', dir: 'asc' };
const DEFAULT_FIELDS = [];

export const getHeaders = () => {
  return {
    'Accept': 'application/json',
    'Content-Type': 'application/json',
    'Authorization': 'Bearer ' + storage.getUser().token
  };
}

export const initHub = (onChangeCollection) => {
  const connection = new signalR.HubConnectionBuilder()
    .withUrl(`${config.url.store}/hub`, { accessTokenFactory: () => storage.getUser().refreshToken })
    .configureLogging(signalR.LogLevel.Error)
    .build();

  connection.start().catch(err => {
    console.error(err)
  });

  connection.on("change", onChangeCollection);
}

export const refreshToken = async () => {
  const url = `${config.url.auth}/api/User/refresh`;
  const refreshToken = storage.getUser().refreshToken;

  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + refreshToken
    }
  });

  if (response.status === 200) {
    const user = await response.json();
    user.refreshToken = refreshToken;
    storage.setUser(user);
    return true;
  }
  else {
    return false;
  }
}

export const getById = async (baseUrl, id) => {
  const url = `${baseUrl}/${id}`;

  const response = await fetch(url, {
    headers: getHeaders(),
  });

  if (response.status === 200) {
    return await response.json();
  }
  else if (response.status === 404) {
    return null;
  }
  else if (response.status === 401 && await refreshToken()) {
    return await getById(baseUrl, id)
  }
  else {
    throw new Error(response.statusText);
  }
}

export const getReport = async (baseUrl, id, properties, filename) => {
  const url = `${baseUrl}/${id}?properties=${JSON.stringify(properties)}`;

  const response = await fetch(url, {
    headers: getHeaders(),
  });

  if (response.status === 200) {
    const res = await response.blob();
    saveAs(res, `${filename}.docx`);
  }
  else if (response.status === 404) {
    return null;
  }
  else if (response.status === 401 && await refreshToken()) {
    return await getReport(baseUrl, id, properties)
  }
  else {
    throw new Error(response.statusText);
  }
}

export const get = async (baseUrl, filters = DEFAULT_FILTERS, sort = DEFAULT_SORT, page = DEFAULT_PAGE, fields = DEFAULT_FIELDS) => {
  const url = `${baseUrl}?filters=${JSON.stringify(filters)}&page=${JSON.stringify(page)}&sort=${JSON.stringify(sort)}&fields=${JSON.stringify(fields)}`;

  const response = await fetch(url, {
    headers: getHeaders(),
  });

  if (response.status === 200) {
    const res = await response.json();
    res.filters = filters;
    res.sort = sort;
    res.page = page;
    res.fields = fields;
    return res;
  }
  else if (response.status === 401 && await refreshToken()) {
    return await get(baseUrl, filters, sort, page, fields);
  }
  else {
    throw new Error(response.statusText);
  }
}

