import config from '../config.json';
import * as utils from './utils';
import * as storage from './storage';

const buildUrl = () => {
  return `${config.url.auth}/api/user`;;
}

export const auth = async (username, password, token = '') => {
  const url = `${config.url.auth}/api/user/auth`;

  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      username,
      password,
      token: token
    }),
  });

  if (response.status === 200) {
    const user = await response.json();
    storage.setUser(user);
    return user;
  }
  else if(response.status === 401) {
    return null;
  }
  else {
    throw new Error(response.statusText);
  }
}

const buildRedirectUri = (provider) => {
  return `${process.env.REACT_APP_HOMEPAGE}?provider=${provider}`;
}

export const oAuth = async (provider, code) => {
  const url = `${config.url.auth}/api/user/oauth`;

  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      provider,
      code,
      redirect_uri: buildRedirectUri(provider),
      config: config.configId
    }),
  });

  if (response.status === 200) {
    const user = await response.json();
    storage.setUser(user);
    return user;
  }
  else if(response.status === 401) {
    return null;
  }
  else {
    throw new Error(response.statusText);
  }
}

export const reset = async (username) => {
  const url = `${config.url.auth}/api/user/reset`;

  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      username,
      redirectUri: process.env.REACT_APP_HOMEPAGE,
    }),
  });

  if (response.status !== 200) {
    throw new Error(response.statusText);
  }
}

export const updateOauth = async (provider, code) => {
  const url = `${config.url.auth}/api/user/oauth`;

  const response = await fetch(url, {
    method: 'PUT',
    headers: utils.getHeaders(),
    body: JSON.stringify({
      provider,
      code,
      redirect_uri: buildRedirectUri(provider)
    }),
  });

  if (response.status === 200) {
    return await response.json();
  }
  else if (response.status === 401 && await utils.refreshToken()) {
    return await updateOauth(provider, code);
  }
  else {
    throw new Error(response.statusText);
  }
}

export const generateSecret = async () => {
  const url = `${config.url.auth}/api/user/secret`;

  const response = await fetch(url, {
    method: 'POST',
    headers: utils.getHeaders(),
  });

  if (response.status === 200) {
    return await response.json();
  }
  else if (response.status === 401 && await utils.refreshToken()) {
    return await generateSecret();
  }
  else {
    throw new Error(response.statusText);
  }
}

export const sign = async (data) => {
  const url = `${config.url.auth}/api/user/sign?withQrCode=true`;

  const response = await fetch(url, {
    method: 'POST',
    headers: utils.getHeaders(),
    body: JSON.stringify({
      data
    }),
  });

  if (response.status === 200) {
    return await response.json();
  }
  else if (response.status === 401 && await utils.refreshToken()) {
    return await sign(data);
  }
  else {
    throw new Error(response.statusText);
  }
}

export const verify = async (data, signature) => {
  const url = `${config.url.auth}/api/user/verify`;

  const response = await fetch(url, {
    method: 'POST',
    headers: utils.getHeaders(),
    body: JSON.stringify({
      data,
      signature
    }),
  });

  if (response.status === 200) {
    return true;
  }
  else if (response.status === 400) {
    return false;
  }
  else if (response.status === 401 && await utils.refreshToken()) {
    return await verify(data, signature);
  }
  else {
    throw new Error(response.statusText);
  }
}

export const getUsers = async (filters) => {
  try {
    const sort = { field: 'name', dir: 'asc' };
    const users = await utils.get(buildUrl(), filters, sort);
    return users;
  }
  catch (error) {
    throw error;
  }
}

const userCache = {};
export const getUserCached = async (id) => {
  try {
    if(!userCache[id]) {
      userCache[id] = await getUser(id);
    }
    return userCache[id];
  }
  catch(error) {
    throw error;
  }
}

export const getUser = async (id) => {
  try {
    return await utils.getById(buildUrl(), id);
  }
  catch(error) {
    throw error;
  }
}

export const updateUser = async (id, data) => {
  try {
    const user = await utils.put(buildUrl(), data, id);
    
    const u = storage.getUser();
    storage.setUser({ ...user, token: u.token, refreshToken: u.refreshToken  });
    return user;
  }
  catch(error) {
    throw error;
  }
}