export const getStats = async (filters) => {
  try {
    const group = { field: 'date', op: 'sum', opField: 'duration' };
    const stats = await utils.aggregation(buildUrl(TIME_RECORDS_COLLECTION), group, filters);
    stats.sum = 0;
    stats.data.forEach(x => {
      x.name = (new Date(x._id)).toLocaleDateString('en-US');
      stats.sum += x.val;
    });
    return stats;
  }
  catch (error) {
    throw error;
  }
}

export const getTimeRecords = async (projects, filters, page) => {
  try {
    const sort = { field: 'date', dir: 'desc' };
    const timeRecords = await utils.get(buildUrl(TIME_RECORDS_COLLECTION), filters, sort, page);
    for(const x of timeRecords.data) {
      x.date = x.date ? new Date(x.date) : null;
      x._project = projects.find(p => p._id === x.projectId);
      x._task = x.taskId ? await getTaskCached(x.taskId) : { name: null };
      x._user = await userUtils.getUserCached(x.userId);
    }
    return timeRecords;
  }
  catch (error) {
    throw error;
  }
}

export const getEvents = async (eventTypes, filters, page) => {
  try {
    const sort = { field: 'start', dir: 'desc' };
    const events = await utils.get(buildUrl(EVENTS_COLLECTION), filters, sort, page);
    for(const x of events.data) {
      x.start = x.start ? new Date(x.start) : null;
      x.end = x.end ? new Date(x.end) : null;
      x._user = await userUtils.getUserCached(x.userId);
      x._eventType = eventTypes.find(e => e._id === x.eventTypeId);
    }
    return events;
  }
  catch (error) {
    throw error;
  }
}

