export const getTasks = async (states, filters, page, sort) => {
  try {
    const tasks = await utils.get(buildUrl(TASKS_COLLECTION), filters, sort, page);
    for (const x of tasks.data) {
      //x.project = projects.find(p => p._id === x.projectId);
      x._assignedTo = x.assignedToId ? await userUtils.getUserCached(x.assignedToId) : null;
      x._state = states.find(s => s._id === x.stateId);
    }
    return tasks;
  }
  catch (error) {
    throw error;
  }
}

